#works {
  margin: 2rem auto;
  min-height: calc(100vh-4rem);
  width: 100vw;
  max-width: 60rem;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  text-align: center;
  padding: 0 1rem;
}

.worksTitle {
  margin: 1rem 0;
  font-size: 2.5rem;
  font-weight: 300;
}
.worksDesc {
  font-size: 1.3rem;
  font-weight: 200;
  max-width: 60rem;
  padding: 0 2rem;
}
.worksImgs {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-wrap: wrap;
  width: 100vw;
  max-width: 65rem;
  margin-left: 1rem;
  margin-top: 1rem;
}
