#intro {
  height: calc(90vh- 4rem);
  width: 80vw;
  max-width: 75rem;
  margin: 0 auto;
  overflow: hidden;
  position: relative;
}

.bg {
  margin-top: 6rem;
  width: 13vw;
  /* height: 39vh; */
  /* border-radius: 20px; */
  border-radius: 100px;
  object-fit: cover;
  right: 0;
  top: 0;
  position: absolute;
  z-index: -1;
}

.introContent {
  margin-top: 6rem;
  height: 63vh;
  width: 60vw;
  padding: 1rem;
  font-size: 3vw;

  display: flex;
  flex-direction: column;
}
.hello {
  font-size: 1.5rem;
  font-weight: lighter;
}
.introName {
  color: cyan;
}
.introPara {
  font-size: 1.3rem;
  font-weight: 200;
}

@media screen and (max-width: 480px) {
  .bg {
    margin-right: 0;
    right: 5rem;
    width: 20vw;
    height: 30vh;
    object-fit: cover;
    border-radius: 20px;
  }
  .introContent {
    font-size: 1.5rem;
    margin-top: 3.5rem;
  }
  .introPara {
    font-size: 1rem;
  }
}

@media screen and (max-width: 840px) {
  .bg {
    margin-right: 3.5rem;
    right: 20vw;
    width: 15vw;
    height: 28vh;
    object-fit: cover;
    border-radius: 20px;
  }
  .introContent {
    font-size: 3rem;
  }
}
@media screen and (max-width: 1500px) {
  .bg {
    margin-right: 1.5rem;
    right: 5rem;
    width: 15vw;
    /* height: 50vh; */
    object-fit: cover;
    margin-top: 7rem;
    border-radius: 100px;
  }
  .introContent {
    font-size: 1rem;
    margin-top: 3.5rem;
  }
  .introText {
    font-size: 1.5rem;
  }
}
