#skills {
  overflow: hidden;
  width: 100vw;
  max-width: 65rem;
  margin: 0 auto;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  text-align: center;
}
.skillTitle {
  font-size: 2.5rem;
  font-weight: 300;

  margin-bottom: 1.5rem;
}
.skillDesc {
  font-size: 1.3rem;
  font-weight: 200;
  max-width: 60rem;
  padding: 0 2rem;
}
.skillBars {
  margin: 1.5rem;
  width: 100vw;
  max-width: 80%;
  text-align: left;
}
.skillBar {
  display: flex;
  margin: 1rem;
  padding: 1.5rem;
  border-radius: 0.5rem;
  background: rgb(50, 50, 50);
}

.skillBarText {
  font-size: 1rem;
  font-weight: 200;
}
.skills-heading {
  font-size: 1.9rem;
  font-weight: 200;
  text-align: center;
}
.skillBtn {
  /* margin: 3rem 0; */
  text-decoration: none;
  padding: 0.5rem 2.5rem;
  border: none;
  border-radius: 2rem;
  background: black;
  font-size: 1rem;
  margin-bottom: 5rem;
  color: black;
  /* background: rgb(50, 50, 50); */
}
.color-text {
  color: white;
}

